body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

.content {
  background-color: blueviolet;
  display: flex;
  flex-wrap: row;
  text-align: center;
  margin-top: 1%;
  margin-left: 3%;
  margin-right: 3%;
}

.left {
  background-color: #6da69e;
  padding: 10px;
  flex: 50% 1;
  text-align: auto;
}

.right {
  background-color: rgb(21, 166, 171);
  padding: 10px;
  flex: 50% 1;
  text-align: auto;
}

/* Responsive layout */
@media (max-width: 800px) {
  .flex-item-right,
  .flex-item-left {
    flex: 100% 1;
  }
}

